.heroWrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.heroCarousel {
  position: relative;
  width: 100%;
  height: 100vh;
}

/* Background Carousel */
.bgCarousel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.videoContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s ease;
}

.videoContainer.active {
  opacity: 1;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Small Vertical Carousel */
.fgCarousel {
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translateY(-50%);
  width: 200px;
  height: 300px;
  z-index: 5;
}

.fgVideoContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s ease;
}

.fgVideoContainer.active {
  opacity: 1;
}

.fgVideo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Text Content */
.textContent {
  position: absolute;
  bottom: 15%;
  left: 10%;
  z-index: 10;
  color: white;
}

.title {
  font-size: 2.5rem;
  font-weight: 300;
  letter-spacing: 0.05em;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.cta {
  display: flex;
  gap: 2rem;
}

.caption {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

/* Navigation Controls */
.rightControls {
  position: absolute;
  right: 2rem;
  bottom: 20%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2rem;
  z-index: 10;
}

.nav {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.prev, .next {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0.5rem;
  position: relative;
  overflow: hidden;
}

.prev svg, .next svg {
  width: 0.5rem;
  height: 0.75rem;
  transition: transform 0.3s ease;
}

.prev:hover svg {
  transform: translateX(-2px);
}

.next:hover svg {
  transform: translateX(2px);
}

.divider {
  width: 1px;
  height: 40px;
  background: rgba(255, 255, 255, 0.5);
}

.navHoverEffect {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) scale(0);
  width: 1.5rem;
  height: 1.5rem;
  background: white;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: -1;
}

.prev:hover .navHoverEffect,
.next:hover .navHoverEffect {
  opacity: 0.2;
  transform: translateX(-50%) scale(1.5);
}

.nextUp {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.nextTitle {
  font-size: 1rem;
  margin: 0.5rem 0;
}

.progressBar {
  height: 1px;
  width: 150px;
  background: white;
  transition: width 0.1s linear;
}

/* Expanding Section Container */
.expandContainer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40vh;
  background: white;
  z-index: 15;
  will-change: height;
  overflow: hidden;
}

@media (max-width: 768px) {
  .fgCarousel {
    width: 150px;
    height: 225px;
    left: 5%;
  }
  
  .textContent {
    left: 5%;
    bottom: 10%;
  }
  
  .title {
    font-size: 1.8rem;
  }
  
  .rightControls {
    right: 1rem;
    bottom: 15%;
  }
  
  .nextTitle {
    font-size: 0.875rem;
  }
  
  .progressBar {
    width: 100px;
  }
  
  .expandContainer {
    height: 35vh;
  }
}